webpackJsonp([128934274419811],{

/***/ 390:
/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<h1 id=\"maintainers\"><a href=\"#maintainers\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Maintainers</h1>\n<!-- MarkdownTOC autolink=true bracket=round -->\n<ul>\n<li><a href=\"#pull-requests\">Pull Requests</a></li>\n<li>\n<p><a href=\"#development\">Development</a></p>\n<ul>\n<li><a href=\"#setup\">Setup</a></li>\n<li><a href=\"#scripts\">Scripts</a></li>\n<li><a href=\"#dependencies\">Dependencies</a></li>\n</ul>\n</li>\n<li>\n<p><a href=\"#releases\">Releases</a></p>\n<ul>\n<li><a href=\"#canary-releases\">Canary releases</a></li>\n</ul>\n</li>\n<li>\n<p><a href=\"#changelogs\">Changelogs</a></p>\n<ul>\n<li><a href=\"#github-api-token\">GitHub API Token</a></li>\n<li><a href=\"#pushing-changelogmd\">Pushing <code>CHANGELOG.md</code></a></li>\n</ul>\n</li>\n<li>\n<p><a href=\"#packages\">Packages</a></p>\n<ul>\n<li><a href=\"#list-packages\">List packages</a></li>\n<li><a href=\"#list-updated-packages\">List updated packages</a></li>\n</ul>\n</li>\n</ul>\n<!-- /MarkdownTOC -->\n<h2 id=\"pull-requests\"><a href=\"#pull-requests\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Pull Requests</h2>\n<p>When merging Pull Requests on GitHub, use the <a href=\"https://github.com/blog/2141-squash-your-commits\">squash and merge</a> button, so that our timeline of master branch is linear.</p>\n<p>Also make sure every Pull Request has a <code>PR</code> label, and one of the semver labels (<code>patch</code>, <code>minor</code>, or <code>major</code>).</p>\n<p>These labels later help generate automated changelogs.</p>\n<h2 id=\"development\"><a href=\"#development\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Development</h2>\n<p>We use <a href=\"https://github.com/lerna/lerna/\">Lerna</a> for managing our monorepo. All our packages can be found in <a href=\"./packages\">packages</a> directory.</p>\n<p>To start developing the packages:</p>\n<h3 id=\"setup\"><a href=\"#setup\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Setup</h3>\n<p>Clone the repository, and run:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>$ npm install\n$ npm run bootstrap</code></pre>\n      </div>\n<h3 id=\"scripts\"><a href=\"#scripts\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Scripts</h3>\n<p>Now you can run the <code>npm</code> scripts:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>$ npm run lint\n$ npm run test\n$ npm run cover</code></pre>\n      </div>\n<p>Which are shortcuts for:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>$ ./node_modules/.bin/lerna run lint\n$ ./node_modules/.bin/lerna run test\n$ ./node_modules/.bin/lerna run cover</code></pre>\n      </div>\n<h4 id=\"runnings-scripts-for-individual-packages\"><a href=\"#runnings-scripts-for-individual-packages\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Runnings scripts for individual packages</h4>\n<p>To run npm scripts at individual package level:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>$ ./node_modules/.bin/lerna run --scope frint-react test</code></pre>\n      </div>\n<h3 id=\"dependencies\"><a href=\"#dependencies\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Dependencies</h3>\n<p>We keep all the <code>devDependencies</code> at root <code>package.json</code> for avoiding unnecessary duplication.</p>\n<p>To install dependencies at individual package level, just update their <code>package.json</code> (even if the dependency is a package from this repo itself), and run this from root:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>$ npm run bootstrap</code></pre>\n      </div>\n<p>The <code>bootstrap</code> script takes care of installing and linking the dependencies in your packages.</p>\n<h2 id=\"releases\"><a href=\"#releases\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Releases</h2>\n<p>To publish a new release:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>$ make release</code></pre>\n      </div>\n<p>You will be prompted to select a semver (patch/minor/major), and once chosen, all your packages (that have changed) will be published to <code>npm</code>.</p>\n<h3 id=\"canary-releases\"><a href=\"#canary-releases\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Canary releases</h3>\n<p>If you wish to release your packages as canary versions:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>$ make release-canary</code></pre>\n      </div>\n<p>Doing so would only publish your packages to <code>npm</code>, without affecting your git repository in any way.</p>\n<p>And the versions on <code>npm</code> would look like <code>1.0.0-alpha.12345678</code>, where the last part is your commit hash.</p>\n<p>Canary releases are useful if you want to publish nightly releases for example.</p>\n<h2 id=\"changelogs\"><a href=\"#changelogs\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Changelogs</h2>\n<p>Since we use <a href=\"https://github.com/lerna/lerna/\">lerna</a> for managing our monorepo, we are using <a href=\"https://github.com/lerna/lerna-changelog\">lerna-changelog</a> for generating our changelogs.</p>\n<p>Changelog for a particular version can be generated as follows:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>$ make changelog GITHUB_API_TOKEN=\"...\" FROM_TAG=\"v1.0.0\" TO_TAG=\"v2.0.0\"</code></pre>\n      </div>\n<p>Doing so would output the changelog in your Terminal, which you can later add to the <code>CHANGELOG.md</code> file.</p>\n<h3 id=\"github-api-token\"><a href=\"#github-api-token\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>GitHub API Token</h3>\n<p>You can generate a token <a href=\"https://github.com/settings/tokens/new?description=GitHub%20Changelog%20Generator%20token\">here</a></p>\n<p>Since this is a public repository, you only need <code>public_repo</code> access for the token.</p>\n<h3 id=\"pushing-changelogmd\"><a href=\"#pushing-changelogmd\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Pushing <code>CHANGELOG.md</code></h3>\n<p>Once the <code>CHANGELOG.md</code> file is updated, it is up to you to commit and push it to GitHub.</p>\n<p>There is a handy command available:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>$ make push-changelog</code></pre>\n      </div>\n<p>We also copy the generated changelog text of a specific version to <a href=\"https://github.com/frintjs/frint/releases\">GitHub Releases</a> section.</p>\n<h2 id=\"packages\"><a href=\"#packages\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Packages</h2>\n<h3 id=\"list-packages\"><a href=\"#list-packages\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>List packages</h3>\n<p>Show all the packages with their versions in the repository:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>$ make list-packages</code></pre>\n      </div>\n<h3 id=\"list-updated-packages\"><a href=\"#list-updated-packages\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>List updated packages</h3>\n<p>Show packages that have been updated locally, and needs publishing:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>$ make list-updated</code></pre>\n      </div>","frontmatter":{"path":"/docs/contributing/maintainers","title":"Maintainers guide","importFromGitHub":"frintjs/frint/master/MAINTAINERS.md"}}},"pathContext":{}}

/***/ })

});


